'use-client'
export default function AboutPage() {
  return (
    <div className="min-h-screen bg-gray-50 py-16">
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center">
          <h1 className="text-4xl font-extrabold text-gray-900">About JobTracker</h1>
          <p className="mt-4 text-xl text-gray-600">
            Helping job seekers stay organized and land their dream jobs
          </p>
        </div>

        <div className="mt-16 bg-white rounded-lg shadow-lg p-8">
          <div className="prose max-w-none">
            <h2 className="text-2xl font-bold text-gray-900 mb-4">Our Mission</h2>
            <p className="text-gray-600 mb-6">
              JobTracker was built to solve one of the most frustrating parts of job searching: staying organized. 
              We understand that managing multiple applications, deadlines, and follow-ups can be overwhelming, 
              and that's where we come in.
            </p>

            <h2 className="text-2xl font-bold text-gray-900 mb-4">What We Do</h2>
            <p className="text-gray-600 mb-6">
              Our platform automatically scrapes job listings from various sources, helps you track your applications, 
              and ensures you never miss a follow-up opportunity. Whether you're a recent graduate or an experienced 
              professional, JobTracker adapts to your job search needs.
            </p>

            <h2 className="text-2xl font-bold text-gray-900 mb-4">Key Features</h2>
            <ul className="list-disc list-inside text-gray-600 mb-6 space-y-2">
              <li>Automated job scraping from multiple job boards</li>
              <li>Application status tracking and management</li>
              <li>Resume storage and organization</li>
              <li>Interview scheduling and reminders</li>
              <li>Analytics and insights on your job search</li>
              <li>Secure data storage with Supabase</li>
            </ul>

            <h2 className="text-2xl font-bold text-gray-900 mb-4">Get Started Today</h2>
            <p className="text-gray-600">
              Ready to take control of your job search? Sign up today and experience the difference 
              an organized approach can make in landing your next opportunity.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}

